export const GRID_SIZE = 20; //columns 
export const CELL_SIZE = 20; //rows

/**
 * User input
 * Code - key code as to which key it is
 * Movement - which way the pacman goes
 * Rotation - which way the pacman faces
 */
export const DIRECTIONS = {
  ArrowLeft: {
    code: 37,
    movement: -1,
    rotation: 180
  },
  ArrowUp: {
    code: 38,
    movement: -GRID_SIZE,
    rotation: 270
  },
  ArrowRight: {
    code: 39,
    movement: 1,
    rotation: 0
  },
  ArrowDown: {
    code: 40,
    movement: GRID_SIZE,
    rotation: 90
  }
};


